/* Общие стили */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Шапка */
.app-header {
  background-color: #2c3e50;
  color: white;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-header h1 {
  margin: 0;
  font-size: 1.5rem;
}

.logout-button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.logout-button:hover {
  background-color: #c0392b;
}

/* Навигация */
.app-nav {
  background-color: #34495e;
  padding: 0.5rem 1rem;
  display: flex;
  gap: 1rem;
}

.nav-link {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.nav-link:hover {
  background-color: #2c3e50;
}

/* Основное содержимое */
.app-main {
  flex: 1;
  padding: 1rem;
}


/* адаптивы */
@media (max-width: 660px) {
  .app-nav {
    flex-direction: column;
    gap: 0.5rem;
  }

  .nav-link {
    padding: 0.75rem 1rem;
    text-align: center;
  }

  .app-header {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
}
